
import Vue from "vue";
export default Vue.extend({

    props: {
        size: Number
    },
    computed: {
        lineStyle() {
            return {
                width: `${this.size}px`,
                height: `${this.size as number / 6}px`,
                margin: `${this.size as number / 6}px 0`
            };
        }
    }

});
